.header_DSP {
  border-bottom: 1px solid rgb(221, 221, 221);
  height: 100px;
  background-color: rgba(255, 255, 255, 0.459);
}
.nav_DSP {
  border: 1px solid rgb(221, 221, 221);
  box-shadow: 0 3px 6px 0 rgba(117, 116, 116, 0.589);
  background: #fff;
  border-radius: 50px;
}
.btn_DSP {
  width: 50px;
  height: 50px;
  border-radius: 50px;
  line-height: 50px;
  text-align: center;
  background-color: #ff385c;
  cursor: pointer;
}
.price-BnB {
  margin-left: 48px;
  font-size: 18px;
}
