  display: flex;
  justify-content: space-evenly;
}

input[name="gender"] {
  display: none;
}
.gender-detail {
  cursor: pointer;
  font-weight: 400;
  font-size: 17px;
  display: block;
}
.gender-detail .gender-otp {
  display: inline-flex;
  align-items: center;
  padding: 4px 16px 4px 10px;
  border-radius: 24px;
  transition: 0.25s ease;
}
.gender-detail .gender-otp:hover,
input[name="gender"]:checked + .gender-otp {
  background-color: #d6d6e5;
}
.gender-detail .gender-otp::before {
  content: "";
  background-color: #fff;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  margin-right: 8px;
  transition: 0.25s ease;
  box-shadow: inset 0 0 0 2px #00005c;
}

input[name="gender"]:checked + .gender-otp::before {
  box-shadow: inset 0 0 0 6px #00005c;
}

