/* buttons hover */
button:hover {
  transform: translatey(3px);
  box-shadow: none;
}

/* buttons hover Animation */
button:hover {
  animation: ani9 0.4s ease-in-out infinite alternate;
}
@keyframes ani9 {
  0% {
    transform: translateY(3px);
  }
  100% {
    transform: translateY(5px);
  }
}

input[id="birthday1"]::before {
  content: attr(placeholder);
}

.category-type {
  display: flex;
  /* justify-content: space-evenly; */
  flex-direction: column;
}

input[name="type"] {
  display: none;
}
.type-detail {
  cursor: pointer;
  font-weight: 400;
  font-size: 17px;
  display: block;
}

.type-detail .type-otp {
  display: inline-flex;
  align-items: center;
  padding: 2px 10px 2px 10px;
  border-radius: 24px;
  transition: 0.25s ease;
}
.type-detail .type-otp:hover,
input[name="type"]:checked + .type-otp {
  background-color: #d6d6e5;
}
.type-detail .type-otp::before {
  content: "";
  background-color: #fff;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  margin-right: 8px;
  transition: 0.25s ease;
  box-shadow: inset 0 0 0 2px #00005c;
}
input[name="type"]:checked + .type-otp::before {
  box-shadow: inset 0 0 0 6px #00005c;
}
